body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html,
body {
  width: 100%;
  background: #eeeeee;
  font-family: Arial, Helvetica, sans-serif
}

header {
  background: #ffe600;
}

.content-center {
  padding: 0 30px;
  clear: both;
}

.text-gray {
  color: #666666;
}

.message-empty-result{
 
  text-align: center

}


.menu-top {
  display: flex;
}

.menu-top form {
  width: 100%;
  color: #999999;
  position: relative;
  margin: 8px 0;
}
.menu-top input[type="search"] {
  width: 100%;
  display: block;
  padding: 8px 9px;
  right: 0;
  margin: 0;
  border: none;
}

.menu-top button {
  width: 34px;
  height: 32px;
  margin-top: 0;
  position: absolute;
  z-index: 2;
  right: 0;
  border: none;
  top: 0
}

.menu-top button img {
  margin-top: 4px;
}

.menu-top .logo {
  margin-top: 6px;
  margin-right: 16px;
}

.App-header {
  background: #ffe600;
}
.title {
  color: #3333333;
}
.text-large {
  color: #6666666;
}

/*::::::::::Breadcrumb:::::::::::*/

#breadcrumb {
  display: inline-block;
  margin: 0;
  padding-left: 0;
}
#breadcrumb > li {
  float: left;
  list-style: none;
  padding: 16px 4px 16px 0;
  color: #666666;
}

#breadcrumb > li:after {
  content: " > ";
  display: inline-block;
  margin: 0px 5px;
}

#breadcrumb > li:last-child:after {
  content: "";
}

/*:::::::::::::Products:::::::::::*/

.product-list {
  clear: both;
  background: #eeeeee;
}

.item-description {
  float: left;
  color: #333333;
  max-width: 50%;
}
.item-description h2 {
  margin-bottom: 32px;
  margin-top: 25px;
}

.list-item {
  clear: both;
  width: 100%;
  background: #fff;
  overflow: hidden;
}

.currency-type {
  display: inline-block;
  margin-right: 8px;
}

.list-item > a > img {
  float: left;
  width: 180px;
  height: 180px;
  margin: 16px;
}

.item-right {
  float: right;
  color: #999999;
  width: 40%;
  text-align: center;
  margin-top: 37px;
}

/*::::::::::::Product details:::::::::::::::*/


@media screen and (max-width: 835px) {
  .product-col-1{
    
    width: 100% !important;

  }

  .product-col-2{
    width: 95% !important;
    clear: both;
    margin: 0;
    padding: 18px;
    float: none !important;
  }
}

.product-col-1 img {
  clear: both;
  height: 291px;
  display: block;
  margin: 0 auto;
}

.product-detail .price{
  display: block
}

.currency-type-detail{
  margin-right: 10px;

}

.product-col-1 {
  width: 80%;
  float: left;
}

.product-col-1 h2 {
  font-size: 28px;
}
.product-col-1 p {
  font-size: 16px;
}
.product-col-2 {
  width: 15%;
  float: right;
  margin-right: 32px;
  background: #fff;
}

.bg-white {
  background: #fff;
  overflow: hidden;
}

.product-col-1 > div {
  margin: 32px;
}
.product-col-1 > h2 {
  margin: 32px;
}

.product-title {
  font-size: 24px;
  clear: both;
  
}

.title-description{
  color:#333333
}

.price {
  font-size: 46px;
  clear: both;
  margin: 32px 0;
}

.text-small {
  color: #666666;
  font-size: 14px;
  clear: both;
  margin-top: 32px;
  display: inline-block;
  margin-bottom: 16px;
}

.btn-primary {
  background: #3483fa;
  text-align: center;
  display: inline-block;
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: solid 1px #3483fa;
  color: #fff;
}

